<template>
  <div>
    <fTitle>
      <template #title>Calendar 日历</template>
      <template #introduce>显示日期</template>
    </fTitle>
    <sTitle>
      <template #title>基本</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content"><el-calendar v-model="value">
</el-calendar></div>
    </el-col></el-row>
    </div></div>
    <sTitle>
      <template #title>自定义内容</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-calendar>
  <template
    slot="dateCell"
    slot-scope="{data}">
    <p :class="data.isSelected ? 'is-selected' : ''">
      {{ data.day.split('-').slice(1).join('-') }} {{ data.isSelected ? '✔️' : ''}}
    </p>
  </template>
</el-calendar>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      value: new Date()
    }
  }
}
</script>

