@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
